import Vue from 'vue'
import App from './App'
import router from './router'
import './style/index.scss'
import 'vue2-animate/dist/vue2-animate.min.css'
import THREE from "three";
import TWEEN from "tween";
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



